import { ChangeEvent } from "react";
import { menu, MenuOptionType } from "../models/menu";

interface MenuOptionProps {
    id: string,
    option: MenuOptionType,
    checked: boolean,
    onChange: (option: MenuOptionType) => void
}

const MenuOption: React.FC<MenuOptionProps> = ({id, option, checked, onChange}) => {

    const htmlId = `${option}-${id}`;
    const inputName = `menu-choice-${id}`;
    const optionString = menu[option]; 

    const changeHandler = (e: ChangeEvent<HTMLInputElement>) => {
        onChange(e.target.value as MenuOptionType);
    }

    return (
        <label htmlFor={htmlId}>
            <input 
                type="radio" 
                name={inputName} 
                defaultChecked={checked} 
                id={htmlId} 
                value={optionString} 
                onChange={changeHandler}
            />
            <div className='menu-option box'>
                <span>{optionString}</span>
            </div>
        </label>
    )
}
export default MenuOption;