import { ChangeEventHandler, useState } from "react";
import {menu, MenuOptionType} from '../models/menu';
import Person from '../models/person';
import MenuOption from './MenuOption';

import './Menu.scss';

const MenuChoice: React.FC<{person: Person}> = ({person}) => {
    const [chosenMenu, setChosenMenu] = useState<string>(menu.standard);

    const submitMenuHandler = (chosenOption: MenuOptionType) => {
        setChosenMenu(chosenOption);
    }

    const menuChoices = person.isChild ? 
        <div className="kids box">
            <span>Детско меню</span>
        </div> : 
        <>
            <p>Избери меню</p>
            <MenuOption id={person.id} option="standard" checked={true} onChange={submitMenuHandler}/>
            <MenuOption id={person.id} option="vegetarian" checked={false} onChange={submitMenuHandler} />
        </>

    return (
        <div className="menu-choice">
            {menuChoices}
        </div>
    )
}

export default MenuChoice;