import PersonClass from "../models/person";
import Person from "./Person";

import './Group.scss';

const Group: React.FC<{ people: PersonClass[] }> = ({ people }) => {
  const groupEls = people.map((person) => (
    <Person person={person} key={person.name} />
  ));
  return (
    <section>
      <h2>Вашата група</h2>
      <ul className="group">{groupEls}</ul>
    </section>
  );
};

export default Group;
