import { ChangeEvent, useState } from "react";
import RSVP from './RSVP';
import MenuChoice from './MenuChoice';
import PersonClass from "../models/person";

import './Person.scss';

const Person: React.FC<{ person: PersonClass }> = ({ person }) => {
  const [chosenMenu, setChosenMenu] = useState<string>(person.menu);
  const [isAttending, setIsAttending] = useState<boolean>(person.RSVP);

  const submitMenuHandler = (e: ChangeEvent) => {
    const target = e.target as HTMLSelectElement;
    setChosenMenu(target.value);
  };


  const rsvpHandler = (checked: boolean): void => {
    setIsAttending(checked)
  }

  return (
    <li className="person">
      <p className="name">{person.name}</p>
      <form>
        <RSVP label="Ще присъствам" checked={false} onClick={rsvpHandler} />
      </form>
      {isAttending && <MenuChoice person={person} />}
    </li>
  );
};

export default Person;
