import { useState } from 'react';
import './RSVP.scss';

interface RSVPProps {
    label: string;
    checked: boolean;
    onClick: (willAttend: boolean) => void;
}

const RSVP: React.FC<RSVPProps> = ({ label, checked, onClick }) => {
    const [willAttend, setWillAttend] = useState(checked)

    const clickHandler = () => {
        setWillAttend(!willAttend)
        onClick(!willAttend)
    }

    return (
        <label className="toggle toggle_label">
            <input className="toggle toggle_input" type="checkbox" defaultChecked={willAttend} onClick={clickHandler} />
            <span className="toggle toggle_span"/>
            <strong>{label}</strong>
        </label>
    )
}

export default RSVP;