const SongForm: React.FC = () => {
  return (
    <form>
      <label htmlFor="song">Ще танцувам, ако пуснете тази песен:</label>
      <input type="text" id="song" />
      <label htmlFor="karaoke">Ще пея, ако пуснете тази песен:</label>
      <input type="text" id="karaoke" />
    </form>
  );
};

export default SongForm;
