import { useState } from "react";
import Login from "./components/pages/Login";
import Welcome from "./components/pages/Welcome";
import Guest from "./models/guest";

import "./App.scss";
import guests from "./store/fake-backend";

const App: React.FC = () => {
  const [loginError, setLoginError] = useState<string | false>(false);
  const [loggedIn, setLoggedIn] = useState<Guest | false>(false);
  const [shouldAnimate, setShouldAnimate] = useState<boolean>(false);

  const loginHandler = (code: string) => {
    const enteredGuest = guests.find((guest) => guest.code === code);
    if (!enteredGuest) {
      setLoginError(
        "No such guest. Please write your name and code exactly as they appear on your invite."
      );
    } else {
      setShouldAnimate(true);
      setTimeout(() => {
        setLoggedIn(enteredGuest);
        setShouldAnimate(false);
      }, 3000);
    }
  };

  const content = loggedIn ? (
    <Welcome user={loggedIn} />
  ) : (
    <>
      <Login
        onSubmit={loginHandler}
        error={loginError}
        shouldAnimate={shouldAnimate}
      />
    </>
  );
  return content;
};

export default App;
