import React, { ChangeEvent, useRef, useState } from "react";
import Logo from "../Logo";

import "./Login.scss";

const Login: React.FC<{
  onSubmit: (code: string) => void;
  error?: string | false;
  shouldAnimate: boolean;
}> = (props) => {
  const [shouldValidate, setShouldValidate] = useState<boolean>(false);
  const [pass, setPass] = useState<string>("");
  const passRef = useRef<HTMLInputElement>(null);

  const changeHandler = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target!.value;
    if (!value) {
      setShouldValidate(true);
    }
    setPass(value);
  };

  const submitHandler = (e: React.FormEvent): void => {
    e.preventDefault();
    const value = passRef.current!.value;
    props.onSubmit(value + "");
  };

  const blurHandler = () => {
    setShouldValidate(true);
  };

  const formText = (
    <p className={props.error ? "errorText" : ""}>
      {props.error || "Въведете кода за достъп от гърба на поканата"}
    </p>
  );

  const form = (
    <form className="form" onSubmit={submitHandler}>
      <label htmlFor="code">{formText}</label>
      <input
        id="code"
        type="text"
        ref={passRef}
        value={pass}
        onBlur={blurHandler}
        onChange={changeHandler}
        className={pass ? "" : shouldValidate ? "invalid" : ""}
      />
      <button
        type="submit"
        disabled={!pass}
        className={pass ? "valid" : "invalid"}
      >
        Вход
      </button>
    </form>
  );

  return (
    <div>
      <Logo shouldAnimate={props.shouldAnimate} />
      {!props.shouldAnimate && form}
    </div>
  );
};

export default Login;
