import logo from "../assets/us.svg";
import "./Logo.scss";
const Logo: React.FC<{ shouldAnimate: boolean }> = (props) => {
  const logoClass = `logo ${props.shouldAnimate ? "loading" : ""}`;

  return (
    <div className={logoClass}>
      <img className="logo-image" src={logo} alt=""></img>
    </div>
  );
};

export default Logo;
